import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { EvolutionMapper } from '../../../../models/evolution.model';

/**
 * Generated class for the PatientEvolutionDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name: 'PatientEvolutionDetailPage'
})
@Component({
  selector: 'page-patient-evolution-detail',
  templateUrl: 'evolution-detail.html',
})
export class PatientEvolutionDetailPage {

  public evolution: EvolutionMapper;

  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl : ViewController) {
    this.evolution = this.navParams.get('evolution');
    console.log('this.evolution', this.evolution);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PatientEvolutionDetailPage');
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }

}



